.profile {
  display: flex;
  flex-grow: 1;
  position: relative;
}

.profile__form {
  width: 100%;
  display: flex;
  flex-direction: column;
  padding: 70px 30px 0;
  margin: 0 auto;
}

.profile__title {
  font-weight: 500;
  font-size: 24px;
  line-height: 29px;
  margin: 0;
  text-align: center;
}

.profile__container {
  display: flex;
  flex-direction: column;
  margin: 80px 0 379px;
}

.profile__label {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  border-bottom: 1px solid #424242;
  padding: 16px 0;
}

.profile__label:first-of-type {
  padding-top: 0;
}

.profile__label:last-of-type {
  border: none;
  padding-bottom: 0;
}

.profile__label-container {
  display: flex;
  width: 100%;
  justify-content: space-between;
}

.profile__input-name {
  min-width: 130px;
  font-weight: 500;
  line-height: 13px;
  margin: 0;
}

.profile__input {
  width: 120px;
  background-color: transparent;
  border: none;
  color: #ffffff;
  outline: none;
  text-align: right;
  font-size: 11px;
  line-height: 13px;
}
.profile__input_invalid{
  color: #EE3465;
}
.profile__input:focus{
  outline: none;
}

.profile__edit-btn {
  font-size: 12px;
  line-height: 15px;
  color: #ffffff;
  border: none;
  background-color: transparent;
  cursor: pointer;
  margin: 0 0 16px;
}
.profile__edit-btn:disabled{
  color: #424242;
  cursor: default;
  opacity: 1;
}

.profile__signout-btn {
  font-weight: 500;
  font-size: 12px;
  line-height: 15px;
  color: #ee3465;
  border: none;
  background-color: transparent;
  cursor: pointer;
}



@media screen and (min-width: 600px) {
  .profile__form {
    max-width: 410px;
    padding: 236px 0 0;
  }

  .profile__container {
    margin: 96px 0 211px;
  }

  .profile__edit-btn {
    font-size: 13px;
    line-height: 16px;
  }

  .profile__signout-btn {
    font-size: 13px;
    line-height: 16px;
  }
  .profile__input {
    width: 300px;
  }
}

@media screen and (min-width: 1280px) {
  .profile__form {
    padding: 74px 0 0;
  }
  .profile__container {
    margin: 123px 0 221px;
  }
  .profile__signout-btn {
    margin-bottom: 70px;
  }
}
