.about-project {
    margin: 87px auto 0 auto;
    padding-bottom: 110px;
    max-width: 1140px;
  }
  
  .about-project__title {
    display: block;
    font-weight: normal;
    font-size: 22px;
    line-height: 27px;
    letter-spacing: -0.04em;
    border-bottom: 1px #dadada solid;
    padding: 23px 0;
  }
  
  .about-project__subtitle {
    font-weight: normal;
    font-size: 20px;
    line-height: 24px;
    letter-spacing: -0.04em;
    margin-bottom: 26px;
  }
  
  .about-project__block:last-of-type {
    margin-left: 53px;
  }
  
  .about-project__description {
    font-weight: normal;
    font-size: 14px;
    line-height: 20px;
    margin: 0;
  }
  
  .about-project__table {
    margin-top: 50px;
    display: flex;
  }
  
  .about-project__timeline {
    display: flex;
    margin-top: 92px;
    justify-content: center;
  }
  
  .about-project__backend {
    width: 15vw;
    text-align: center;
  }
  
  .about-project__backend-title {
    background-color: #3ddc84;
    height: 36px;
    font-weight: normal;
    font-size: 14px;
    line-height: 17px;
    color: black;
    margin-bottom: 13px;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .about-project__frontend {
    width: 60vw;
    text-align: center;
  }
  
  .about-project__frontend-title {
    height: 36px;
    background-color: #303030;
    font-weight: normal;
    font-size: 14px;
    line-height: 17px;
    margin-bottom: 13px;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .about-project__weeks {
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    color: #8b8b8b;
  }
  
  @media screen and (max-width: 1210px) {
    .about-project {
      max-width: 1010px;
    }
  }
  
  @media screen and (max-width: 1023px) {
    .about-project {
      max-width: 669px;
      margin: 69px auto 0 auto;
      padding-bottom: 92px;
    }
    .about-project__grid {
      gap: 1px 33px;
    }
    .about-project__timeline {
      margin-top: 69px;
    }
    .about-project__backend {
      width: 140px;
    }
    .about-project__frontend {
      width: 528px;
    }
    .about-project__block:last-of-type {
      margin-left: 39px;
    }
    .about-project__subtitle {
      margin-bottom: 19px;
    }
  }
  
  @media screen and (max-width: 767px) {
    .about-project {
      max-width: 282px;
      margin: 47px auto 0 auto;
    }
    .about-project__title {
      font-size: 18px;
      line-height: 22px;
      padding: 25px 0;
      margin: 0;
    }
    .about-project__grid {
      margin-top: 50px;
      display: grid;
      grid-template-columns: 1fr;
      gap: 5px 47px;
    }
    .about-project__table {
      flex-direction: column;
    }
    .about-project__subtitle {
      font-size: 18px;
      line-height: 22px;
      margin: 12px 0;
    }
    .about-project__description {
      font-size: 11px;
      line-height: 16px;
      margin: 14px 0;
    }
    .about-project__block {
      margin-bottom: 35px;
    }
    .about-project__timeline {
      margin-top: 0;
    }
    .about-project__backend {
      width: 31vw;
    }
    .about-project__frontend {
      width: 57vw;
    }
    .about-project__backend-title {
      font-size: 11px;
      line-height: 13px;
      margin-bottom: 4px;
    }
    .about-project__frontend-title {
      font-size: 11px;
      line-height: 13px;
      margin-bottom: 4px;
    }
    .about-project__weeks {
      font-size: 11px;
      line-height: 13px;
    }
    .about-project__block:last-of-type {
      margin-left: 0;
    }
  }
  